	module.exports = {"data":{"allPostTitles":{"edges":[{"node":{"frontmatter":{"title":"Express","doc":3,"section":3,"type":"doc"},"fields":{"slug":"/express"}}},{"node":{"frontmatter":{"title":"FAQ","doc":5,"section":1,"type":"doc"},"fields":{"slug":"/faq"}}},{"node":{"frontmatter":{"title":"Quick Start","doc":1,"section":1,"type":"doc"},"fields":{"slug":"/quick-start"}}},{"node":{"frontmatter":{"title":"Packages","doc":1,"section":2,"type":"doc"},"fields":{"slug":"/packages"}}},{"node":{"frontmatter":{"title":"Generators","doc":3,"section":1,"type":"doc"},"fields":{"slug":"/generators"}}},{"node":{"frontmatter":{"title":"React Native","doc":2,"section":3,"type":"doc"},"fields":{"slug":"/react-native"}}},{"node":{"frontmatter":{"title":"Redux","doc":1,"section":3,"type":"doc"},"fields":{"slug":"/redux"}}},{"node":{"frontmatter":{"title":"Standalone","doc":2,"section":2,"type":"doc"},"fields":{"slug":"/standalone"}}},{"node":{"frontmatter":{"title":"Templates","doc":2,"section":1,"type":"doc"},"fields":{"slug":"/templates"}}}]},"postBySlug":{"html":"<p>If you don't have Node.js installed you can install the <code>hygen</code> binary in standalone mode. Depending on your operating system, you have a few options.</p>\n<h2 id=\"macos\"><a href=\"#macos\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>MacOS</h2>\n<p>You can install via Homebrew:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code>$ brew tap jondot/tap\n$ brew install hygen</code></pre>\n      </div>\n<h2 id=\"linux-windows-macos-non-homebrew\"><a href=\"#linux-windows-macos-non-homebrew\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Linux, Windows, MacOS (Non-homebrew)</h2>\n<p>You can download a binary for your operating system <a href=\"https://github.com/jondot/hygen/releases\">on the Github releases page</a>. Binaries are updated and pushed automatically with every new version.</p>\n<p>After downloading the tarball, place it in your preferred operating system <code>bin</code> path.</p>\n<h2 id=\"why-standalone\"><a href=\"#why-standalone\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Why Standalone?</h2>\n<p>There are a few reasons to want to run <code>hygen</code> as standalone:</p>\n<ul>\n<li>You're not familiar with Node.js or don't want to have it installed.</li>\n<li>You prefer a global binary to avoid installing the same binary over and over.</li>\n<li>You're packing <code>hygen</code> into your own software, Docker image, or re-distributing it.</li>\n<li>You're building tooling <em>over</em> <code>hygen</code> and need a maleable binary to execute.</li>\n<li>The standalone package is a little bit faster (because it snapshots code). You want that extra speed.</li>\n</ul>","timeToRead":1,"excerpt":"If you don't have Node.js installed you can install the  hygen  binary in standalone mode. Depending on your operating system, you have aâ€¦","frontmatter":{"title":"Standalone"},"fields":{"slug":"/standalone"}}},"pathContext":{"slug":"/standalone"}}

